import React, { Fragment, useEffect, useState } from 'react'
import IntlMessage from 'components/util-components/IntlMessage'
import { Link, useLocation } from 'react-router-dom'
import { classNames } from 'utils'
import { ChevronDownIcon } from '@heroicons/react/outline'
import { Transition } from '@headlessui/react'

const MenuItem = ({ menu, onClick }) => {

    const location = useLocation()
    const [subnav, setSubnav] = useState(false);

    function handleSubNav () {
        setSubnav(prevData => !prevData)
    };

    useEffect(() => {
        function onLocationChange() {
            menu.submenu.map((item) => (
                location.pathname === item.path && setSubnav(true) 
            ))
        }
        if (menu.path === '#') {
            onLocationChange()   
        }
    }, [location, menu])

    return (
        <li className='my-2'>
            { menu.submenu.length > 0 ?
                <div
                    onClick={handleSubNav}
                    className={classNames(
                        subnav ? 'bg-white/[0.10]' :
                        '',
                        'group w-full p-2 rounded-3 flex justify-start items-center text-xs font-medium hover:text-white hover:cursor-pointer'
                    )}
                >
                    <menu.icon 
                        className={classNames(
                            subnav ? 'fill-white' : 'fill-slate-300',
                            'h-8 w-8 group-hover:fill-white'
                        )}
                    />
                    <div className='flex justify-between w-full items-center'>
                        <span 
                            className={classNames(
                                subnav ? 'text-white' : ' text-slate-300 group-hover:text-white',
                                'text-sm font-semibold ml-3'
                            )}
                        ><IntlMessage id={menu.title} /></span>
                        <ChevronDownIcon 
                            className={classNames(
                                subnav ? 'text-white rotate-180 ' : 'text-slate-300',
                                'h-4 w-4 transform transition'
                            )}
                        />
                    </div>
                </div>
            :
                <Link
                    className={classNames(
                        location.pathname.includes(menu.path)? 'bg-emind-300' 
                        : subnav ? 'bg-white/[0.04]' :
                        '',
                        'group w-full p-2 rounded-3 flex flex-row justify-start text-xs font-medium hover:text-white'
                    )}
                    onClick={onClick}
                    to={menu.path}
                >
                    <menu.icon 
                        className={classNames(
                            location.pathname.includes(menu.path) ? 'fill-white' : 'fill-slate-300',
                            'h-8 w-8 group-hover:fill-white'
                        )}
                    />
                    <span 
                        className={classNames(
                            location.pathname.includes(menu.path) ? 'text-white' : ' text-slate-300 group-hover:text-white',
                            'text-sm font-semibold ml-3'
                        )}
                    ><IntlMessage id={menu.title} /></span>
                </Link>

            }
            <Transition.Root as={Fragment} show={subnav}>
                <Transition.Child
                    enter='transition ease-out duration-100'
                    enterFrom='transform opacity-0 scale-95'
                    enterTo='transform opacity-100 scale-100'
                    leave='transition ease-in duration-75'
                    leaveFrom='transform opacity-100 scale-100'
                    leaveTo='transform opacity-0 scale-95'
                >
                    <ul className='bg-white/[0.10] rounded-3 relative mt-2 block'>
                        <Transition.Child
                            enter='transform transition ease-in-out duration-500 sm:duration-700'
                            enterFrom='translate-x-full'
                            enterTo='translate-x-0'
                            leave='transform transition ease-in-out duration-500 sm:duration-700'
                            leaveFrom='translate-x-0'
                            leaveTo='translate-x-full'
                        >
                            { menu.key !== 'configs' && menu.submenu.map((submenu, idx) => (
                                <li className='my-1' key={idx}>
                                    <Link
                                        onClick={onClick}
                                        to={submenu.path}
                                        className={classNames(
                                            location.pathname === submenu.path ? 'bg-emind-300' : '',
                                            'group w-full p-2 rounded-3 flex flex-row justify-start text-xs font-medium hover:text-white'
                                        )}
                                    >
                                        <submenu.icon 
                                            className={classNames(
                                                location.pathname === submenu.path ? 'fill-white' : 'fill-slate-300',
                                                'h-8 w-8 group-hover:fill-white'
                                            )}
                                        />
                                        <span 
                                            className={classNames(
                                            location.pathname === submenu.path ? 'text-white' : ' text-slate-300 group-hover:text-white',
                                            'text-sm font-semibold ml-3'
                                            )}
                                        ><IntlMessage id={submenu.title} /></span>
                                    </Link>
                                </li>
                            ))}
                        </Transition.Child>
                    </ul>
                </Transition.Child>
            </Transition.Root>
		</li>
    )
}

export default MenuItem