import { useState, useEffect } from 'react';
import {
  ClassesSVG,
  ConfigSVG,
  ConversationSVG,
  EmployeesSVG,
  EventSVG,
  HomeSVG,
  LiveSVG,
  OnlineFileSVG,
  OnlineLessonSVG,
  PlanSVG,
  ProgramSVG,
  ReportSVG,
  RoutineSVG,
  SchoolSchemaSVG,
  SchoolSVG,
  StudentReportSVG,
  StudentSVG,
  SubjectSVG,
  SubSchoolSVG,
  TeacherSVG,
  UsersSVG
} from 'assets/svg/menu-icon';
import { CheckPer } from './checkPermission';

export function useMenu() {

  const [userNav, setUserNav] = useState([])
  const [configNav, setConfigNav] = useState([])

  const [isRefetch, setIsRefetch] = useState(true)

  const isLoading = CheckPer("loading")

  const permissions = {
    view_home: CheckPer("view_home"),
    view_student: CheckPer("view_student"),
    view_teacher: CheckPer("view_teacher"),
    view_employee: CheckPer("view_employee"),
    view_live: CheckPer("view_live"),
    view_school: CheckPer("view_school"),
    view_sub_school: CheckPer("view_sub_school"),
    view_program: CheckPer("view_program"),
    view_classes: CheckPer("view_classes"),
    view_subject: CheckPer("view_subject"),
    view_event: CheckPer("view_event"),
    view_conversation: CheckPer("view_conversation"),
    view_plan: CheckPer("view_plan"),
    view_report: CheckPer("view_report"),
    view_online_file: CheckPer("view_online_file"),
    view_online_lesson: CheckPer("view_online_lesson"),
    view_routine: CheckPer("view_routine"),
    view_student_report: CheckPer("view_student_report"),
    configs: CheckPer("configs"),
    view_planmark: CheckPer("view_planmark"),
    view_event_type: CheckPer("view_event_type"),
    view_question_level: CheckPer("view_question_level"),
    view_group: CheckPer("view_group"),
    view_teacher_status: CheckPer("view_teacher_status"),
    view_student_status_extra: CheckPer("view_student_status_extra"),
    view_student_status: CheckPer("view_student_status"),
    view_classtime: CheckPer("view_classtime"),
    view_activity: CheckPer("view_activity"),
    view_schoolyear: CheckPer("view_schoolyear"),
    view_degree: CheckPer("view_degree"),
  }

  const mainNavTree = [
    {
      "key": "view_stdudent",
      "path": "#",
      "title": "users",
      "icon": UsersSVG,
      "priority": 1,
      "breadcrumb": true,
      "submenu": [
        {
          "key": "view_teacher",
          "path": "/app/teacher",
          "title": "teacher",
          "icon": TeacherSVG,
          "priority": 2,
          "breadcrumb": true,
          "submenu": []
        },
        {
          "key": "view_student",
          "path": "/app/student",
          "title": "student",
          "icon": StudentSVG,
          "priority": 2,
          "breadcrumb": true,
          "submenu": []
        },
        {
          "key": "view_employee",
          "path": "/app/employees",
          "title": "employees",
          "icon": EmployeesSVG,
          "priority": 1000,
          "breadcrumb": true,
          "submenu": []
        },
      ]
    },
    {
      "key": "view_subject",
      "path": "#",
      "title": "school-schema",
      "icon": SchoolSchemaSVG,
      "priority": 4,
      "breadcrumb": true,
      "submenu": [

        {
          "key": "view_school",
          "path": "/app/school",
          "title": "school",
          "icon": SchoolSVG,
          "priority": 5,
          "breadcrumb": true,
          "submenu": []
        },
        {
          "key": "view_sub_school",
          "path": "/app/sub-school",
          "title": "sub-school",
          "icon": SubSchoolSVG,
          "priority": 6,
          "breadcrumb": true,
          "submenu": []
        },
        {
          "key": "view_program",
          "path": "/app/programs",
          "title": "program",
          "icon": ProgramSVG,
          "priority": 7,
          "breadcrumb": true,
          "submenu": []
        },
        {
          "key": "view_classes",
          "path": "/app/classes",
          "title": "classes",
          "icon": ClassesSVG,
          "priority": 8,
          "breadcrumb": true,
          "submenu": []
        },
        {
          "key": "view_subject",
          "path": "/app/subject",
          "title": "subject",
          "icon": SubjectSVG,
          "priority": 9,
          "breadcrumb": true,
          "submenu": []
        },
      ]
    },
    {
      "key": "view_live",
      "path": "/app/live",
      "title": "live",
      "icon": LiveSVG,
      "priority": 4,
      "breadcrumb": true,
      "submenu": []
    },
    {
      "key": "view_event",
      "path": "/app/event",
      "title": "event",
      "icon": EventSVG,
      "priority": 4,
      "breadcrumb": true,
      "submenu": []
    },
    {
      "key": "view_conversation",
      "path": "/app/conversation",
      "title": "conversation",
      "icon": ConversationSVG,
      "priority": 4,
      "breadcrumb": true,
      "submenu": []
    },
    {
      "key": "view_plan",
      "path": "/app/plan",
      "title": "plan",
      "icon": PlanSVG,
      "priority": 6,
      "breadcrumb": true,
      "submenu": []
    },
    {
      "key": "view_report",
      "path": "#",
      "title": "report",
      "icon": ReportSVG,
      "priority": 10,
      "breadcrumb": true,
      "submenu": [
        // {
        //   "key": "view_report",
        //   "path": "/app/report/dashboard",
        //   "title": "dashboard",
        //   "icon": ReportSVG,
        //   "priority": 10,
        //   "breadcrumb": true,
        //   "submenu": []
        // },
        {
          "key": "view_report",
          "path": "/app/report/consolidated-report",
          "title": "consolidated-report",
          "icon": ReportSVG,
          "priority": 10,
          "breadcrumb": true,
          "submenu": []
        },
        {
          "key": "view_report",
          "path": "/app/report/attendance",
          "title": "attendance-report",
          "icon": ReportSVG,
          "priority": 10,
          "breadcrumb": true,
          "submenu": []
        },
      ]
    },
    {
      "key": "view_online_lesson",
      "path": "#",
      "title": "online-lesson",
      "icon": OnlineLessonSVG,
      "priority": 4,
      "breadcrumb": true,
      "submenu": [
        {
          "key": "view_online_file",
          "path": "/app/online-file",
          "title": "online-file",
          "icon": OnlineFileSVG,
          "priority": 12,
          "breadcrumb": true,
          "submenu": []
        },
        {
          "key": "view_online_lesson",
          "path": "/app/online-lesson",
          "title": "online-lesson",
          "icon": OnlineLessonSVG,
          "priority": 13,
          "breadcrumb": true,
          "submenu": []
        },
      ]
    },
    {
      "key": "view_routine",
      "path": "/app/routine",
      "title": "routine",
      "icon": RoutineSVG,
      "priority": 101,
      "breadcrumb": true,
      "submenu": []
    },
    {
      "key": "view_student_report",
      "path": "/app/student-report",
      "title": "student-report",
      "icon": StudentReportSVG,
      "priority": 103,
      "breadcrumb": true,
      "submenu": []
    },
    {
      "key": "configs",
      "path": "/app/configs",
      "title": "configs",
      "icon": ConfigSVG,
      "breadcrumb": true,
      "submenu": []
    }
  ]

  const configNavTree = [
    {
      "key": "view_degree",
      "path": "degree",
      "title": "degree",
      "breadcrumb": true,
      "submenu": []
    },
    {
      "key": "view_schoolyear",
      "path": "schoolyear",
      "title": "schoolyear",
      "breadcrumb": true,
      "submenu": []
    },
    {
      "key": "view_activity",
      "path": "activity",
      "title": "activity",
      "breadcrumb": true,
      "submenu": []
    },
    {
      "key": "view_classtime",
      "path": "class-times",
      "title": "class-times",
      "breadcrumb": true,
      "submenu": []
    },
    {
      "key": "view_student_status",
      "path": "student-status",
      "title": "student-status",
      "breadcrumb": true,
      "submenu": []
    },
    {
      "key": "view_student_status_extra",
      "path": "student-status-extras",
      "title": "student-status-extras",
      "breadcrumb": true,
      "submenu": []
    },
    {
      "key": "view_teacher_status",
      "path": "teacher-status",
      "title": "teacher-status",
      "breadcrumb": true,
      "submenu": []
    },
    {
      "key": "view_group",
      "path": "group-permission",
      "title": "group-permission",
      "breadcrumb": true,
      "submenu": []
    },
    {
      "key": "view_event_type",
      "path": "event-type",
      "title": "event-type",
      "breadcrumb": true,
      "submenu": []
    },
  ]

  function refetch() {
    setIsRefetch(true)
    setUserNav([])
  }

  useEffect(() => {
    if (isLoading !== 'loading' && isRefetch) {
      mainNavTree.map(nav => {
        if (nav.submenu.length > 0) {
          const subNav = nav.submenu.filter(subNav => permissions[subNav.key])
          if (subNav.length > 0) {
            setUserNav(prevNav => [
              ...prevNav,
              {
                "key": nav.key,
                "path": nav.path,
                "title": nav.title,
                'icon': nav.icon,
                "breadcrumb": true,
                "submenu": subNav
              }
            ])
          }
        } else if (permissions[nav.key]) {
          setUserNav(prevNav => [...prevNav, nav])
        }
        return null
      })
      configNavTree.map(nav => {
        if (permissions[nav.key]) {
          setConfigNav(prevNav => [...prevNav, nav])
        }
        return null
      })
      setIsRefetch(false)
    }

  }, [isLoading, isRefetch, mainNavTree, configNavTree, permissions])

  return {
    mainNavTree: [...
      [{
        "key": "view_home",
        "path": "/app/home",
        "title": "home",
        "icon": HomeSVG,
        "priority": 1,
        "breadcrumb": true,
        "submenu": []
      }], ...userNav],
    configNavTree: configNav,
    refetch: refetch
  }

}